<template>
    <div>
        <div v-if="mode === 'DESIGN'">
            <el-select class="max-fill" v-if="!expanding" size="medium" v-model="_value" disabled
                       :placeholder="placeholder"/>
            <el-radio-group v-model="_value" v-else>
                <el-radio disabled v-for="(op, index) in optionsShow" :key="index" :label="op.key">{{ op.value }}</el-radio>
            </el-radio-group>
        </div>
        <div v-else>
            <el-select class="max-fill" v-if="!expanding" v-model="_value" size="medium"
                       :clearable="!perm||perm==='E'" :disabled="perm&&perm==='R'"
                       :placeholder="placeholder">
                <el-option v-for="(op, index) in optionsShow" :key="index" :value="op.key" :label="op.value"></el-option>
            </el-select>
            <el-radio-group v-model="_value" v-else>
                <el-radio :disabled="perm&&perm==='R'" v-for="(op, index) in optionsShow" :key="index" :label="op.key">
                    {{ op.value }}
                </el-radio>
            </el-radio-group>
        </div>
    </div>
</template>

<script>
import componentMinxins from '../ComponentMinxins'
import {selectOptions} from '@/api/form'

export default {
    mixins: [componentMinxins],
    name: "SelectInput",
    components: {},
    props: {
        value: {
            type: String,
            default: null
        },
        placeholder: {
            type: String,
            default: '请选择选项'
        },
        expanding: {
            type: Boolean,
            default: false
        },
        options: {
            type: Array,
            default: () => {
                return []
            }
        },
        perm: {
            type: String,
            default: 'E'
        },
        optionsFromRemote: {
            type: Boolean,
            default: false
        },
        remoteUrl: {
            type: String,
            default: ''
        }
    },
    data() {
        return {
            remoteOptions:[]
        }
    },
    computed:{
        //计算属性绑定金额
        optionsShow(){
            if(this.optionsFromRemote&&this.remoteUrl.length>0) {
                return this.remoteOptions;
            }

            return this.options;
        },
    },
    mounted() {

        if(this.optionsFromRemote&&this.remoteUrl.length>0&&this.mode != 'DESIGN'){

            selectOptions({
                remoteUrl:this.remoteUrl
            }).then(res=>{
                console.log(res)
                var data = res.data;
                this.remoteOptions=data;
            })
        }
    },
    methods: {}
}
</script>

